/* style={{ width: '332px'}}
 */
 element.style {
    display: block;
}

[type=reset], [type=submit], button, html [type=button] {
    --webkit-appearance: button;
}

.slick-prev {
    left: -25px;
}

.slick-next, .slick-next:focus, .slick-next:hover, .slick-prev, .slick-prev:focus, .slick-prev:hover {
    color: transparent;
    outline: none;
    background: transparent;
}
.slick-next, .slick-prev {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    /* display: block; */
    width: 20px;
    height: 20px;
    padding: 0;
    -ms-transform:
     translateY(-50%);
    transform: translateY(-50%);
    cursor: pointer;
    border: none;
}
button, select {
    text-transform: none;
}
button, input {
    overflow: visible;
}
button, input, optgroup, select, textarea {
    font: inherit;
    margin: 0;
}
*, :after, :before {
    box-sizing: inherit;
}
.slick-next:before, .slick-prev:before {
    color: #000;
}

.slick-prev:before {
    content: "\2190";
}
.slick-next:before, .slick-prev:before {
    font-family: slick;
    font-size: 20px;
    line-height: 1;
    opacity: .75;
    /* color: #fff; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
*, :after, :before {
    box-sizing: inherit;
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    padding: 5px;
    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}

.slick-dots li, .slick-dots li button {
    width: 20px;
    height: 20px;
    cursor: pointer;
}

button, select {
    text-transform: none;
}
button, input {
    overflow: visible;
}
/* button, input, optgroup, select, textarea {
    font: inherit;
    margin: 0;
} */
.slick-dots {
    position: absolute;
    bottom: -25px;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    /* text-align: center; */
}

ol, ul {
    margin: 0 0 1.45rem 1.45rem;
    padding: 0;
